.header {
  display: flex;
  align-items: center;
}

.header__menu {
  margin-left: 60px;
}

.header__nav {
  display: flex;
  gap: 30px;
}
.header__nav li a {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
  color: #010D14;
  transition: 0.3s color;
}
.header__nav li a:hover {
  color: #FF6200;
}

.nav__buttons {
  margin-left: auto;
  display: flex;
  align-items: center;
}

.btn__outline {
  display: inline-block;
  padding: 15px 15px;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  text-decoration: none;
  text-transform: uppercase;
  color: #03283C;
  margin-right: 15px;
  transition: 0.3s color;
}
.btn__outline:hover {
  color: #FF6200;
}

.btn__primary {
  display: inline-block;
  padding: 15px 30px;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #03283C;
  color: #FFFFFF;
  transition: 0.3s background-color;
}
.btn__primary:hover {
  background-color: #010D14;
}

.header__link {
  padding: 20px 15px;
}
.header__link.active {
  color: #FF6200;
}

.submenu__arrow {
  position: absolute;
  transform: translate(-15px, -4px);
  transition: 0.3s ease-in;
}
.submenu__path.active {
  fill: #FF6200;
}

.menu__item.with__submenu {
  position: relative;
}
@media (max-width: 1117px) {
  .menu__item.with__submenu {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
}
.menu__item.with__submenu .submenu {
  display: none;
  position: absolute;
  left: 0;
  right: 0;
  top: 25px;
  background: #03283C;
}
.menu__item.with__submenu .submenu.active {
  display: block;
  animation: fade 0.3s linear;
}
@media (max-width: 1117px) {
  .menu__item.with__submenu .submenu.active {
    position: static;
  }
}
.menu__item.with__submenu .submenu__link {
  color: white;
  text-transform: none;
  display: flex;
  padding: 15px;
  transition: 0.3s background-color;
}
.menu__item.with__submenu .submenu__link:hover {
  background-color: #FF6200;
}

.header__burger {
  display: none;
  position: relative;
  width: 30px;
  height: 20px;
  z-index: 3;
  margin-left: auto;
}

.header__burger span {
  position: absolute;
  background-color: #010D14;
  left: 0;
  width: 100%;
  height: 2px;
  top: 9px;
  transition: all 0.3s ease 0s;
}

.header__burger:before,
.header__burger:after {
  content: "";
  background-color: #010D14;
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  transition: all 0.3s ease 0s;
}

.header__burger:before {
  top: 0;
}

.header__burger:after {
  bottom: 0;
}

.header__burger.active:before {
  transform: rotate(45deg);
  top: 9px;
}

.header__burger.active:after {
  transform: rotate(-45deg);
  bottom: 9px;
}

.header__burger.active span {
  transform: scale(0);
}

@media (max-width: 1250px) {
  .logo {
    height: 69px;
  }
  .logo img {
    position: relative;
    top: 50%;
    transform: translateY(-60%);
    width: 200px;
  }
  .header__nav {
    gap: 20px;
  }
  .header__nav {
    display: flex;
    gap: 30px;
  }
  .header__nav li a {
    font-size: 15px;
  }
  .btn__outline {
    padding: 12px 15px;
    font-weight: 700;
    font-size: 16px;
  }
  .btn__primary {
    padding: 12px 25px;
    font-weight: 700;
    font-size: 16px;
  }
}
@media (max-width: 1117px) {
  .nav__buttons {
    display: none;
  }
  .header__burger {
    display: block;
  }
  .header:before {
    content: "";
    position: absolute;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    background: #5e5373;
    z-index: -1;
  }
  .header__nav {
    display: flex;
    position: relative;
    top: 40%;
    flex-direction: column;
    align-items: center;
    gap: 40px;
  }
  .header__nav li a {
    font-weight: 400;
    color: white;
    text-decoration: none;
    font-size: 24px;
  }
  .header__menu {
    display: block;
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: #03283C;
    transition: all 0.6s ease 0s;
    margin-left: 0;
    z-index: 1;
  }
  .header__menu.active {
    top: 0;
  }
  .header__burger.active:after,
.header__burger.active:before {
    background-color: white;
  }
  .nav__buttons.active {
    display: flex;
    z-index: 2;
    position: absolute;
    right: 0;
    left: 0;
    bottom: 0;
    gap: 20px;
  }
  .btn__primary {
    flex: 0 1 50%;
    text-align: center;
    background: #FF6200;
  }
  .btn__outline {
    flex: 1 1 50%;
    text-align: center;
    background: #FF6200;
    margin-right: 0;
    color: white;
  }
  #a {
    order: 1;
  }
  #b {
    order: 3;
  }
}
@media (max-width: 480px) {
  .nav__buttons.active {
    flex-wrap: wrap;
    gap: 10px;
  }
  .btn__primary {
    flex: 0 1 100%;
    padding: 15px 0;
  }
}
